     url('./fonts/TerraIgnota.woff') format('woff');\n    font-weight: 600;\n    font-style: normal;\n  }\n\n/*------- VARIABLES -------*/\n\n// ------- COLOR PALETTE -------//\n$border-color: rgba(182, 182, 182, 0.4);\n$main-dark: rgb(0, 0, 0);\n$main-dark: rgb(0, 10, 6);\n$main-light: rgba(245, 245, 245, 0.8);\n$trans-background_aqua: rgba(82, 206, 189, 0.1);\n$trans-background_orange: rgba(255, 102, 0, 0.1);\n$trans-background_green: rgba(153, 252, 140, 0.1);\n$test-color: rgb(224, 118, 228);\n\n// ------- TYPOGRAPHY -------//\n:root {\n    \n    font: {\n        family: TerraIgnota;\n        size: 18px;\n    }\n}\n\n.wrapper {\n    background-image: url('../media/icons/oldtime_map_background.jpg');\n    background-size: cover;\n    overflow: hidden;\n}\n\nhtml, body {\n    margin: 0;\n    min-height: 100%;\n}\n\nh1, h2, h3, h4 {\n    font-family: TerraIgnota;\n    color: $main-dark;\n}\n\nh1 {\n    font: {\n        size: 4rem;\n        family: 'Fredericka the Great';\n    }\n    margin: 0;\n    padding-left: 1rem;\n    padding-bottom: 0.5rem;\n}\n\nh2 {\n    font-size: 3.5rem;\n    margin: 0;\n}\n\n* {\n    box-sizing: border-box;\n    border-radius: 6px;\n}\n\n// ----- PRELIMINARY PRINT FORMATTING ----- //\n@media print {\n    .section-container {\n                border: fuchsia;\n                color: fuchsia;\n            }\n\n    header, footer, form {\n        display: none;\n    }\n}\n\n@page {\n    margin: 2cm;\n}\n\n","@import 'base';\n\n.wrapper {\n    padding: 0;\n    display: grid;\n    grid-gap: 1px;\n    grid-template-columns: repeat(12, 1fr);\n    grid-template-rows: 80px auto auto 140px;\n    grid-template-areas:\n    \"hd hd hd hd hd hd hd hd hd hd hd hd\"\n    \"ef ef ef ef ef ef ef ef ef ef ef ef\"\n    \"if if if if if if cd cd cd cd cd cd\"\n    \"if if if if if if cd cd cd cd cd cd\"\n    \"cf cf cf cf wr wr wr wr tl tl tl tl\"\n    \"cf cf cf cf wr wr wr wr pl pl pl pl\"\n    \"f f f f f f f f f f f f\";\n}\n\n.section-container_img-facts {\n    grid-area: if;\n}\n\n.section-container_entry-form {\n    grid-area: ef;\n}\n\n.section-container_weather {\n    grid-area: wr;\n}\n\n.section-container_countdown {\n    grid-area: cd;\n    background-image: url('../media/icons/steampunk-clock_image_15.png');\n    background-size: cover;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    text-align: center;\n}\n\n.section-container_facts {\n    grid-area: cf;\n}\n\n.section-container_pack-list {\n    grid-area: pl;\n}\n\n.section-container_trip-length {\n    grid-area: tl;\n    background-color: $trans-background_green;\n}\n\n.section-container_header {\n    grid-area: hd;\n}\n\n.section-container_footer {\n    grid-area: f;\n}\n\nbody {\n    font: {\n        family: 'TerraIgnota';\n        size: 18px;\n    }\n    background-color: $main-light;\n    color: #444;\n    width: 100vw;\n    margin: 0;\n}\n\nbutton {\n    padding: 1em;\n    width: 180px;\n    border: none;\n    border-radius: 4px;\n    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);\n    background-color: $border-color;\n}\n\ndiv {\n    padding: 1em;\n\n    // background-color: $main-light;\n}\n\nbutton:hover {\n    color: $main-dark;\n    background-color: $main-light;\n    transition: ease-in 0.2s;\n}\n\nform {\n    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);\n    max-width: 100%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    padding: 0.5rem;\n    list-style-type: none;\n}\n\nimg {\n    width: 100%;\n    height: auto;\n}\n\nli {\n    background-color: $main-light;\n    margin: 0;\n    list-style-type: none;\n    cursor: pointer;\n    border-top: 1px solid #e5e5e5;\n    font-size: 1.5rem;\n    padding: 1rem 2rem;\n}\n\nsection {\n    background-color: rgba(245, 245, 245, 0.6);\n    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.8);\n\n}\n\nul {\n    list-style-type: none;\n}\n\n\n\n.close {\n    float: right;\n    padding: 0.4em;\n    border: solid rgba(128, 128, 128, 0.4);\n}\n\n.countdown-digits {\n    font: {\n        family: 'Fredericka the Great';\n        size: 3rem;\n    }\n}\n\n.current-weather {\n    background-color: $trans-background_aqua;\n}\n\n.hidden {\n    display: none;\n}\n\n.input {\n    border: none;\n    color: #444;\n    font-size: 2rem;\n    padding: 1rem;\n    display: block;\n    margin: auto;\n    width: 60%;\n}\n\n.input::placeholder {\n    color: #d5d5d5;\n}\n\n.input:focus {\n    outline-color: rgb(190, 147, 67);\n}\n\ndiv#forecast {\n    display: flex;\n    padding: 1em;\n    border-radius: 4px;\n    background-color: rgba(255, 102, 0, 0.1);\n    justify-content: space-between;\n    gap: 3.5em;\n    text-align: center;\n    overflow: scroll;\n    box-shadow: 1px 1px 6px 1px rgba(128, 128, 128, 0.4);\n\n    li {\n        border-radius: 4px;\n        box-shadow: 1px 1px 12px 1px rgba(128, 128, 128, 0.4);\n    }\n}\n\ndiv#forecast::-webkit-scrollbar {\n    display: none;\n}\n\n#flag {\n    width: 50%;\n    height: auto;\n}\n\n#pack-list-item {\n    // padding: none;\n    // margin: 0;\n    width: 64%;\n}\n\n.modal {\n    display: none; /* Hidden by default */\n    position: fixed; \n    z-index: 1; \n    padding-top: 5vh; \n    left: 0;\n    top: 0;\n    width: 100vw; \n    height: 100vh; \n    overflow: auto; \n    background-color: rgb(0,0,0); \n    background-color: rgba(0,0,0,0.6); \n  }\n  \n  .modal-content {\n    margin: auto;\n    background-image: url('../media/icons/oldtime_map_background.jpg');\n    // mask-image: rgba(245, 245, 245, 0.6);\n    padding: 20px;\n    border: 1px solid #888;\n    overflow: auto;\n    width: 80%;\n    height: 80%;\n  }\n\n\n\nli.checked-off {\n    color: #b6b6b6;\n    text-decoration: line-through;\n}\n  \n  /* The Close Button */\n  .close {\n    color: #aaaaaa;\n    float: right;\n    font-size: 28px;\n    font-weight: bold;\n  }\n  \n  .close:hover,\n  .close:focus {\n    color: #000;\n    text-decoration: none;\n    cursor: pointer;\n  }"],"sourceRoot":""}]);
